:root {
  --main-text-color: #000;
  --accent-color: #f7941e;
  --secondary-color: #fff;
  --additional-color: #231f20;
  --main-font-family: "Montserrat", sans-serif;
  --secondary-font-family: "Roboto", sans-serif;
  --bgd-color: #f1f1f2;
  --bgd-card: #fafafa;
  --hover-effect: 250ms cubic-bezier(.4, 0, .2, 1);
}

body {
  font-family: var(--main-font-family);
  background-color: var(--secondary-color);
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

button {
  cursor: pointer;
  font-family: inherit;
}

.link {
  text-decoration: none;
}

img {
  display: block;
}

h1, h2, h3, h4, p {
  margin: 0;
}

.container {
  width: 1200px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

.page-header .container {
  align-items: center;
  display: flex;
}

.nav {
  margin-left: auto;
}

.nav-list {
  gap: 31px;
  display: flex;
}

.nav-link {
  color: var(--main-text-color);
  padding-top: 45px;
  padding-bottom: 45px;
  font-size: 18px;
  line-height: 1.22;
  display: block;
}

.nav-link:hover, .nav-link:focus, .current {
  color: var(--accent-color);
}

.header-btn {
  color: var(--accent-color);
  border: 1px solid var(--accent-color);
  text-transform: uppercase;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 5px;
  margin-left: auto;
  padding: 14px 20px;
  font-size: 18px;
  line-height: 1.22;
}

.header-btn:hover, .header-btn:focus {
  color: var(--secondary-color);
  background-color: var(--accent-color);
}

